import React from 'react';
import { BrowserRouter as Router, Route} from "react-router-dom"

import Navbar from "./components/navbar.component";
import Index from "./components/index.component";
import Summoner from "./components/summoner.component";
import Sardoche from "./components/sardoche.component";

console.log(process.env.RIOT_KEY);
console.log(process.env.REACT_APP_RIOT_KEY);

function App() {
    return (
        <Router>
            <Navbar />
            <Route path="/" exact component={Index} />
            <Route path="/summoner/:name" component={Summoner} />
            <Route path="/sardoche" component={Sardoche} />
        </Router>
        );
    }
    
    export default App;
    