import React, { Component} from "react";
import styles from '../public/css/navbar.module.css';

export default class Navbar extends Component {
    
    render(){
        return (
            <div className={styles.hoverheader}>
                <div className={styles.header}>
                    <div className={styles.m1}></div>
                    <div className={styles.m2}></div>
                    <div className={styles.m3}></div>
                    <div className={styles.m4}></div>
                    <div className={styles.m5}></div>
                </div>
                <div className={styles.bars}></div>
            </div>
        )
    }
}
