import React, { Component} from "react";
import Match from "../components/match.component";
import styles from '../public/css/matches.module.css';


export default class matches extends Component {
    constructor(props){
        super(props);

        this.state = {
            matches : props.matches,
            key : "RGAPI-cf3b2c0c-cd46-46bf-8daa-19ac1f17e3ea",
        }
    }

    matchHistory() {
        if (this.state.matches && this.state.matches.length)
            return this.state.matches.map(match => {
                return <Match match={match} key={match.gameId} summid={this.props.summid}/>
            });
        else
            return ;
    }
    
    render() {
        return (
            <div className={styles.history}>
                {this.matchHistory()}
            </div>
        )
    }
}