import React, { Component} from "react";
import Matches from "../components/matches.component";
const axios = require("axios");
require('dotenv').config()

export default class index extends Component {

    constructor(props){
        super(props);

        //console.log(process.env.RIOT_KEY)
        //console.log(process.env.REACT_APP_RIOT_KEY)
        this.state = {
            summ_status : null,
            summ_accid : null,
            summ_matches : null,
            key : process.env.RIOT_KEY,
            summ_name : props.match.params.name,
        }
        //console.log(this.state)
    }

    componentDidMount() {
        axios.get("https://euw1.api.riotgames.com/lol/summoner/v4/summoners/by-name/" + this.state.summ_name + "?api_key=" + this.state.key)
        .then(res => {
            if (res.data)
            {
                this.setState({summ_accid : res.data.accountId})
                
                axios.get("https://euw1.api.riotgames.com/lol/match/v4/matchlists/by-account/" + this.state.summ_accid + "?endIndex=5&beginIndex=0&api_key=" + this.state.key)
                .then(res => {
                    if (res.data)
                        this.setState({summ_matches : res.data.matches, summ_status : true})
                    }).catch(err =>{
                        this.setState({summ_status : false})
                        if (err)
                            console.log(err)
                    })
            } 
            }).catch(err =>{
                this.setState({summ_status : false})
                if (err)
                    console.log(err)
            })
    }

    
    render() {
        if (this.state.summ_status === false)                    // MAUVAIS NOM
            return (
                <div>
                    <p>summoner not found</p>
                </div>
            )
        else if (this.state.summ_status === null)               // CHARGEMENT
            return (
                <div>
                    <p>loading..</p>
                </div>
            )
        else                                                     // BON RESULTAT
            return (
                <div>
                    <p>searching for {this.state.summ_name}</p>
                    <Matches summid={this.state.summ_accid} matches={this.state.summ_matches}  />
                </div>
            )
    }
}