import React, { Component} from "react";
const axios = require("axios");

export default class matches extends Component {
    constructor(props){
        super(props);

        this.state = {
            accountid : "UEiOs6tdblFgEZiaR5sXrIQubaCM29rIYohJKPaZyr5AiN4xQFyINZFP",
            key : "RGAPI-cf3b2c0c-cd46-46bf-8daa-19ac1f17e3ea",
            pseudo : "chargement.."
        }
    }
    
    componentDidMount() {
        axios.get("https://euw1.api.riotgames.com/lol/summoner/v4/summoners/by-account/UEiOs6tdblFgEZiaR5sXrIQubaCM29rIYohJKPaZyr5AiN4xQFyINZFP?api_key=" + this.state.key)
        .then(res => {
            if (res.data)
                console.log(res.data)
                this.setState({pseudo : res.data.name})
        }).catch(err =>{
            if (err)
                console.log(err)
        })
      }

    render() {
        return (
            <div>
                {this.state.pseudo}
            </div>
        )
    }
}